<template>
<table class="simple-table">
  <thead>
    <tr>
      <th v-for="(item, key) in renderTableHead(tableType)">{{ item.name }}<span v-if="item.can_sort === 1" class="icon sorted ic-arrow-up">&nbsp;</span></th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="(item, key, idx) in listData">
      <td v-for="(item, key, idx) in item">
        <span v-if="key === 'position'" :value="item"><img class="icon" src="~@/../static/img/ic-location.svg"></span>
        <span v-else-if="key === 'options'">
          <Tooltip class="tooltip-orange" content="数据分析" placement="bottom">
            <i v-if="item['analysis'] === true" class="icon icomoon-ic-pie-chart"></i>
          </Tooltip>
          <Tooltip class="tooltip-purple" content="历史数据" placement="bottom">
            <i v-if="item['history'] === true" class="icon icomoon-ic-history-log"></i>
          </Tooltip>
          <!-- <img class="icon" src="~@/../static/img/ic-option.svg"> -->
        </span>
        <span v-else>{{ item }}</span>
      </td>
    </tr>
  </tbody>
</table>
</template>

<script>
  export default {
    data() {
      return {}
    },
    props: ['listData', 'tableType'],
    mounted () {},
    computed: {
      renderTableHead: function () {
        return function (target) {
          if(target == 'fire') {
            return [{name:'设备编号', can_sort:0}, {name:'名称', can_sort:1}, {name:'设备位置', can_sort:0}, {name:'维护人员名称', can_sort:0}, {name:'最近检修日', can_sort:0}, {name:'维保排程', can_sort:0}, {name:'维保状态', can_sort:0}];
          }
          if(target == 'alarmInfo') {
            return [{name:'最新日期', can_sort:1}, {name:'地點設定', can_sort:0}, {name:'设备位置', can_sort:0}, {name:'维护人员名称', can_sort:0}, {name:'告警條件名稱', can_sort:0}, {name:'所有條件狀態', can_sort:0}, {name:'操作', can_sort:0}];
          }
          if(target == 'deviceInfo') {
            return [{name:'#', can_sort:0}, {name:'设备编号', can_sort:1}, {name:'名称', can_sort:1}, {name:'类型', can_sort:1}, {name:'所属用户', can_sort:1}, {name:'所属用户组', can_sort:1}, {name:'最后更新时间', can_sort:1}, {name:'终端位置', can_sort:0}, {name:'数据查看', can_sort:0}];
          }
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .simple-table {
    width: 100%;
    text-align: center;

    thead tr {
      border-radius: 2px;
      background-color: #fefefe;
      box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.24);

      th {
        font-size: 1.5rem;
        padding: 1rem 0.5rem;
      }
    }
    tbody tr {
      td {
        padding: 1.1rem 0.5rem;

        i {
          font-size: 20px;
          padding: 0 0.7rem;

          &.icomoon-ic-pie-chart {
            color: #ff9931;
          }
          &.icomoon-ic-history-log {
            color: #806dff;
          }
        }
      }
    }
  }
</style>


