<template>
<section>
  <h4>
    <Breadcrumb separator=">">
      <BreadcrumbItem>洞察</BreadcrumbItem>
      <BreadcrumbItem v-for="(name, idx) in routerName" :key="idx">{{ name }}</BreadcrumbItem>
    </Breadcrumb>
  </h4>
  <router-view></router-view>
</section>
</template>

<script>
export default {
  name: 'Alarm',
  data() {
    return {}
  },
  computed: {
    routerName: function () {
      return this.$route.name;
    }
  }
}
</script>


