<template>
  <div class="main-content">
    <toolbox-download></toolbox-download>
    <VuePerfectScrollbar>
      <div class="scroll-block">
        <section class="chart-box full-page">
          <h2 class="title">数据总量</h2>
          <div class="chart-container">
            <div class="chart-nav">
              <span class="text-normal" v-for="label in basicPieChart.labels">{{ label.name }}： <span>{{ label.value }}</span> <span>MB</span></span>
            </div>
            <div class="chart" ref="chart_data_amount"></div>
          </div>
        </section>
        <section class="chart-box full-page">
          <h2 class="title">数据成长</h2>
          <div class="chart-container">
            <div class="chart-nav">
              <Row type="flex" justify="start" class="code-row-bg">
                <Col span="15"></Col>
                <Col span="5">
                  <ButtonGroup size="large" class="full-width flex-box">
                    <Button @click="switchDataGrowing('total')" :class="isActiveBtn('dataIncrementType', 'total', 'ivu-btn-primary')">总量</Button>
                    <Button @click="switchDataGrowing('increment')" :class="isActiveBtn('dataIncrementType', 'increment', 'ivu-btn-primary')">增量</Button>
                    <Button @click="switchDataGrowing('inventory')" :class="isActiveBtn('dataIncrementType', 'inventory', 'ivu-btn-primary')">存量</Button>
                  </ButtonGroup>
                </Col>
                <Col span="3">
                  <ButtonGroup size="large" class="full-width flex-box">
                    <Button @click="switchDataGrowing('P1D')" :class="isActiveBtn('dataIncrementPeriod', 'P1D', 'ivu-btn-primary')">当日</Button>
                    <Button @click="switchDataGrowing('P30D')" :class="isActiveBtn('dataIncrementPeriod', 'P30D', 'ivu-btn-primary')">近30天</Button>
                  </ButtonGroup>
                </Col>
                <Col span="1"></Col>
              </Row>
            </div>
            <div class="chart" ref="chart_line"></div>
          </div>
        </section>
        <section class="chart-box full-page">
          <h2 class="title">数据比较</h2>
          <div class="chart-container">
            <div class="chart-nav">
              <Row type="flex" justify="start" class="code-row-bg">
                <Col span="3"></Col>
                <Col span="12">
                  <DatePicker 
                    size="large"
                    style="width: 48%"
                    format="yyyy/MM/dd" 
                    :value="switchBtn.dataComparisonDate2"
                    :type="switchBtn.dataComparisonPeriod == 'date' ? 'date' : 'daterange'"
                    @on-change="getDatePicker('dataComparisonDate2', $event)">
                  </DatePicker>
                  <DatePicker 
                    size="large"
                    class="input-orange" 
                    style="width: 48%"
                    format="yyyy/MM/dd"
                    :value="switchBtn.dataComparisonDate1"
                    :type="switchBtn.dataComparisonPeriod == 'date' ? 'date' : 'daterange'"
                    @on-change="getDatePicker('dataComparisonDate1', $event)">
                  </DatePicker>
                </Col>
                <Col span="5">
                  <ButtonGroup size="large" class="full-width flex-box">
                    <Button @click="switchDataComparison('sum')" :class="isActiveBtn('dataComparisonType', 'sum', 'ivu-btn-primary')">总量</Button>
                    <Button @click="switchDataComparison('add')" :class="isActiveBtn('dataComparisonType', 'add', 'ivu-btn-primary')">增量</Button>
                    <Button @click="switchDataComparison('save')" :class="isActiveBtn('dataComparisonType', 'save', 'ivu-btn-primary')">存量</Button>
                  </ButtonGroup>
                </Col>
                <Col span="3">
                  <ButtonGroup size="large" class="full-width flex-box">
                    <Button @click="switchDataComparison('date')" :class="isActiveBtn('dataComparisonPeriod', 'date', 'ivu-btn-primary')">日期</Button>
                    <Button @click="switchDataComparison('interval')" :class="isActiveBtn('dataComparisonPeriod', 'interval', 'ivu-btn-primary')">区间</Button>
                  </ButtonGroup>
                </Col>
                <Col span="1"></Col>
              </Row>
            </div>
            <div class="chart" ref="chart-data-comparison"></div>
          </div>
        </section>
        <section class="chart-box full-page">
          <h2 class="title">数据成分</h2>
          <div class="chart-container">
            <div class="chart-nav">
              <Row type="flex" justify="start" class="code-row-bg">
                <Col span="4" class="tab-like">
                  <Button @click="switchDataComponent('data-stock')" :class="isActiveBtn('dataComponentIndex', 'data-stock', 'active')">存量</Button>
                </Col>
                <Col span="4" class="tab-like">
                  <Button @click="switchDataComponent('data-increment')" :class="isActiveBtn('dataComponentIndex', 'data-increment', 'active')">增量</Button>
                </Col>
                <Col span="20"></Col>
              </Row>
            </div>
            <div class="chart">
              <Row>
                <Col span="12">
                  <div class="chart-container-height" ref="component-left" ></div>
                </Col>
                <Col span="12">
                  <div class="chart-container-height" ref="component-right"></div>
                </Col>
              </Row>
            </div>
          </div>
        </section>
      </div>
    </VuePerfectScrollbar>
  </div>
</template>

<script>
  import VuePerfectScrollbar from 'vue-perfect-scrollbar';
  import toolboxDownload from '@/components/common/toolbox-download';
  import chart_options from '@/assets/js/chartOptions.js';
  export default {
    name: 'dataSummary',
    components: {
      VuePerfectScrollbar,
      toolboxDownload
    },
    data() {
      return {
        basicPieChart: {
          labels: [{id: 'total', name: '总量', value: 0}, {id: 'increment', name: '增量', value: 0}, {id: 'inventory', name: '存量', value: 0}],
          options: chart_options.pie_basic(), 
          chartData: []
        },
        comparisonChart: {
          options: chart_options.line_multiple_basic()
        },
        componentChart_left: {
          options: chart_options.bar_basic(),
          data: []
        },
        componentChart_right: {
          options: chart_options.bar_basic(),
          data: []
        },
        singleLineChart: {
          options: chart_options.line_single(),
          data: {}
        },
        switchBtn: {
          dataIncrementType: 'total',
          dataIncrementPeriod: 'P1D',
          dataComparisonType: 'sum',
          dataComparisonPeriod: 'date',
          dataComparisonDate1: '',
          dataComparisonDate2: '',
          dataComponentIndex: 'data-stock'
        }
      }
    },
    created: function () {
      let vm = this;
      vm.switchBtn.dataComparisonDate1 = vm.returnDate(null);
      vm.switchBtn.dataComparisonDate2 = vm.returnDate(null);
    },
    computed: {
      deleteId: function() {
        return function(data, id) {
          let newData = data;
          _.forEach(newData, function (val, key) { delete val[id]; });
          return newData;
        }
      },
      returnDate: function () {
        return function (seconds_before) {
          let vm = this;
          let day = vm.$moment().format('YYYY-MM-DD');
          
          if (seconds_before) {
            let seconds_now = vm.$moment().unix();
            day = vm.$moment((seconds_now - seconds_before) * 1000).format('YYYY-MM-DD');
          }
          return day;
        }
      },
      returnDateRange: function() {
        return function (seconds_before_from, seconds_before_to) {
          let vm = this;
          let from = vm.returnDate(seconds_before_from), to = vm.returnDate(seconds_before_to)
          return [from, to];
        }
      },
      returnDays: function () {
        return function (days, date_end) {
          let vm = this;
          let xArr = [];
          let date2second = vm.$moment().unix();

          if(date_end) {
            date2second = vm.$moment(date_end).unix();
          }
          for (let i = 1; i < (days + 1); i++) {
            xArr.push(vm.$moment((date2second - (days - i) * 86400) * 1000).format('MM/DD'));
          }
          return xArr;
        }
      },
      returnSum: function() {
        return function (data, idx) {
          let sum = 0;
          _.forEach(data, function (val, key) { sum += val[idx]; });
          return sum;
        }
      },
      isActiveBtn: function() {
        return function(btnIndex, btnTarget, className) {
          let vm = this;
          return (vm.switchBtn[btnIndex] == btnTarget ? className : '');
        }
      },
      extendChartData: function() {
        return function(options, data) {
          if (Array.isArray(data)) {
            let newSeries = [];
            let series_config = options.series[0];
            for (let i = 0; i < data.length; i++) {
              newSeries.push(_.assignIn({}, series_config, data[i]));
            }
            options.series = newSeries;
          } else if (typeof(data) == 'object') {
            _.assignIn(options.series[0], data);
          } else {
            console.log('Extended data type is illegal. Please use array type or object type. ');
          }
          return options;
        }
      },
      randData: function () {
        return function(range, length) {
          let data = [];
          for(var i = 0; i < length; i++) {
            data.push(Math.ceil(Math.random() * range))
          }
          return data;
        }
      }
    },
    mounted () {
      let vm = this;
      let _data_amount = {
        data: [{id: 'increment', name: '增量', value: 6980}, {id: 'inventory', name: '存量', value: 3310}]
      };
      
      _.forEach(vm.basicPieChart.labels, function (label, labelKey) {
         _.forEach(_data_amount.data, function (dataVal, dataKey) {
          if (label.id == 'total') {
            vm.basicPieChart.labels[labelKey].value = vm.returnSum(_data_amount.data, 'value');
          } else if (label.id == dataVal.id) {
            vm.basicPieChart.labels[labelKey].value = dataVal.value;
          }
        });
      });
      let cData = {data: vm.deleteId(_data_amount.data, 'id')};

      vm.basicPieChart.chartData = [_.assignIn({}, cData, chart_options.pie_label_1()), _.assignIn({}, cData, chart_options.pie_label_2())];
      vm.initChart('chart_data_amount', vm.basicPieChart.options, vm.basicPieChart.chartData);

      // data-comparison
      vm.comparisonChart.options.series[0].data = vm.randData(8000, 24);
      vm.comparisonChart.options.series[1].data = vm.randData(8000, 24);
      vm.switchDataComparison('date');
      // data-component
      let yAxis_data = ['盗窃','消防','污染','房屋交易','土地买满','基础设施','绿化布局','桥梁建设','城市交通','车辆管理','教育资源','粮油储备','综合人力','户籍注册'];
      let test_data = [100,400,312,456,235,123,378,152,471,392,245,220,530,340];
      let bar_data = [];
      test_data.forEach((v,i) => {
        let obj = { name: yAxis_data[i], value:v, countText:v * 2 +'笔' };
        bar_data.push(obj);
      });
      vm.componentChart_left.data = {data: bar_data};
      vm.componentChart_right.data = {data: bar_data};
      vm.componentChart_left.options.yAxis.data = yAxis_data;
      vm.componentChart_right.options.yAxis.data = yAxis_data;
      vm.componentChart_left.options.grid = {
        top: '8%',
        left: '26%',
        bottom: '5%',
      }
      vm.comparisonChart.options.xAxis.nameGap = 35; 
      vm.componentChart_left.options.toolbox = {
        show : true,
        right: '3%',
        bottom: '4%',
        feature: {
          saveAsImage: { show: true }
        }
      }
      vm.componentChart_right = vm.componentChart_left;
      vm.initChart('component-left', vm.componentChart_left.options, vm.componentChart_left.data);
      vm.initChart('component-right', vm.componentChart_right.options, vm.componentChart_right.data);
      // data increasing
      let _data_increasing = {
        'P1D': {
          'total': [150, 200, 211, 254, 267, 392, 512, 470, 543, 301, 398, 420, 490, 692, 513, 626, 553, 575, 603, 845, 622, 332, 424, 455],
          'increment': [0, 0, 0, 0, 0, 112, 212,120, 211, 0, 54, 100, 121, 312, 123, 231, 153, 165, 213, 425,212,0,0,22],
          'inventory': [150, 200, 211, 254, 267, 280, 300,350, 332, 301, 344, 320, 369, 380,390, 395, 400, 410, 390, 420, 410,332,424,433]
        },
        'P30D': {
          'total': [4500, 6000, 6432, 5762, 4781, 6023, 4245, 4730, 7243, 7450, 5694, 5533, 4902, 7012, 4323, 6246, 7153, 8565, 9113, 6425, 7312, 5623, 6536, 6525],
          'increment': [3000,4000 ,4322 , 3222, 2111, 3223, 1245,1230, 4211, 4440, 2254, 2333, 1212, 3212, 1233, 2341, 3153, 4465, 5213, 2225,3212,2321,2332,2222],
          'inventory': [1500, 2000, 2110, 2540, 2670, 2800, 3000,3500, 3032, 3010, 3440, 3200, 3690, 3800,3090, 3905, 4000, 4100, 3900, 4200, 4100,3302,4204,4303]
        }
      };
      vm.singleLineChart.data = _data_increasing;
      vm.singleLineChart.options.grid = {
        left: '10%',
      }
      vm.initChart('chart_line', vm.singleLineChart.options, {data: _data_increasing['P1D'].total});

      window.onresize = function() {
        vm.resizeChart(['chart_data_amount', 'chart_line', 'chart-data-comparison', 'component-left', 'component-right']);
      };
    },
    methods: {
      getDatePicker: function (target, date) {
        let vm = this;

        if (vm.switchBtn.dataComparisonPeriod == 'date') {
          vm.switchBtn[target] = vm.$moment(date).format('YYYY-MM-DD');
          // @TODO: axios by date (dataComparisonDate1, dataComparisonDate2)
          vm.comparisonChart.options.series[0].data = vm.randData(8000, 24);
          vm.comparisonChart.options.series[1].data = vm.randData(8000, 24);
        } else if (vm.switchBtn.dataComparisonPeriod == 'interval') {
          function returnDaysNum (startDay, endDay) {
            let mStartDay = vm.$moment(startDay),
                mEndDay = vm.$moment(endDay),
                dayAmount = ((mEndDay.unix() - mStartDay.unix()) / 86400) + 1;
            return dayAmount;
          }
          let dateArr = [];
          _.forEach(date, function (d, key) {
            dateArr.push(vm.$moment(d).format('YYYY-MM-DD'));
          });
          vm.switchBtn[target] = dateArr;
          // @TODO: axios by daterange (dataComparisonDate1, dataComparisonDate2)
          let data_0, data_1;
          let newArr = [...vm.switchBtn.dataComparisonDate1, ...vm.switchBtn.dataComparisonDate2];
          newArr.sort();

          let totalDays = returnDaysNum(newArr[0], newArr[newArr.length - 1]);
          let target1 = {
            leng1: returnDaysNum(vm.switchBtn.dataComparisonDate1[0], vm.switchBtn.dataComparisonDate1[1]),
            leng2: Math.abs(returnDaysNum(vm.switchBtn.dataComparisonDate1[0], newArr[0]) - 1)
          };
          let target2 = {
            leng1: returnDaysNum(vm.switchBtn.dataComparisonDate2[0], vm.switchBtn.dataComparisonDate2[1]),
            leng2: Math.abs(returnDaysNum(vm.switchBtn.dataComparisonDate2[0], newArr[0]) - 1)
          };
          console.log(target1, target2);
          data_0 = vm.randData(8000, target1.leng1);
          data_1 = vm.randData(8000, target2.leng1);
          for (let i=0; i<target1.leng2; i++) {
            data_0.unshift(null);
          }
          for (let i=0; i<target2.leng2; i++) {
            data_1.unshift(null);
          }
          vm.comparisonChart.options.xAxis.data = vm.returnDays(totalDays, newArr[newArr.length - 1]);
          vm.comparisonChart.options.series[0].data = data_0;
          vm.comparisonChart.options.series[1].data = data_1;
        }
        vm.initChart('chart-data-comparison', vm.comparisonChart.options);
      },
      initChart: function (chartId, options, _data) {
        let vm = this;
        let chart = vm.$echarts.init(vm.$refs[chartId]);

        chart.setOption(vm.extendChartData(options, _data));
      },
      resizeChart: function (chartId) {
        let vm = this;

        _.forEach(chartId, function (val, key) {
          let chart = vm.$echarts.init(vm.$refs[val]);
          chart.resize();
        });
      },
      switchDataGrowing: function (flg) {
        let vm = this;
        switch(flg) {
          case 'total':
            vm.singleLineChart.options.series[0].itemStyle.color = '#6ba0ff';
            vm.initChart('chart_line', vm.singleLineChart.options, {data: vm.singleLineChart.data[vm.switchBtn.dataIncrementPeriod].total});
            vm.switchBtn.dataIncrementType = 'total';
            break;
          case 'increment':
            vm.singleLineChart.options.series[0].itemStyle.color = '#7fffaa';
            vm.initChart('chart_line', vm.singleLineChart.options, {data: vm.singleLineChart.data[vm.switchBtn.dataIncrementPeriod].increment});
            vm.switchBtn.dataIncrementType = 'increment';
            break;
          case 'inventory':
            vm.singleLineChart.options.series[0].itemStyle.color = '#00ffff';
            vm.initChart('chart_line', vm.singleLineChart.options, {data: vm.singleLineChart.data[vm.switchBtn.dataIncrementPeriod].inventory});
            vm.switchBtn.dataIncrementType = 'inventory';
            break;
          case 'P1D':
            vm.singleLineChart.options.xAxis.name = '时';
            vm.initChart('chart_line', vm.singleLineChart.options, {data: vm.singleLineChart.data['P1D'][vm.switchBtn.dataIncrementType]});
            vm.switchBtn.dataIncrementPeriod = 'P1D';
            break;
          case 'P30D':
            vm.singleLineChart.options.xAxis.name = '日';
            vm.initChart('chart_line', vm.singleLineChart.options, {data: vm.singleLineChart.data['P30D'][vm.switchBtn.dataIncrementType]});
            vm.switchBtn.dataIncrementPeriod = 'P30D';
            break;
        }
      },
      switchDataComparison: function(flg) {
        let vm = this;
        let data_0, data_1;

        switch (flg) {
          case 'sum':
          case 'add':
          case 'save':
            if (vm.switchBtn.dataComparisonType == 'interval') {
              data_0 = vm.randData(8000, 8);
              data_1 = vm.randData(8000, 8);
            } else {
              data_0 = vm.randData(8000, 24);
              data_1 = vm.randData(8000, 24);
            }
            vm.comparisonChart.options.series[0].data = data_0;
            vm.comparisonChart.options.series[1].data = data_1;
            vm.initChart('chart-data-comparison', vm.comparisonChart.options);
            vm.switchBtn.dataComparisonType = flg;                
            break;
          case 'date':
            vm.comparisonChart.options.xAxis.data = ['0:00', '1:00', '2:00', '3:00', '4:00', '5:00', '6:00', '7:00', '8:00', '9:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00', '22:00', '23:00'];
            vm.comparisonChart.options.xAxis.name = '时';
            vm.comparisonChart.options.tooltip.formatter = '时间: {b}<br>数据: {c} KB';
            data_0 = vm.randData(8000, 24);
            data_1 = vm.randData(8000, 24);
            vm.comparisonChart.options.series[0].data = data_0;
            vm.comparisonChart.options.series[1].data = data_1;
            vm.initChart('chart-data-comparison', vm.comparisonChart.options);
            vm.switchBtn.dataComparisonPeriod = flg;
            vm.switchBtn.dataComparisonDate1 = vm.returnDate(null);
            vm.switchBtn.dataComparisonDate2 = vm.returnDate(null);
            break;
          case 'interval':
            vm.comparisonChart.options.xAxis.data = vm.returnDays(8, null);
            vm.comparisonChart.options.xAxis.name = '日';
            vm.comparisonChart.options.tooltip.formatter= '日期: {b}<br>数据: {c} KB';
            data_0 = vm.randData(8000, 8);
            data_1 = vm.randData(8000, 8);
            vm.comparisonChart.options.series[0].data = data_0;
            vm.comparisonChart.options.series[1].data = data_1;
            vm.initChart('chart-data-comparison', vm.comparisonChart.options);
            vm.switchBtn.dataComparisonPeriod = flg;
            vm.switchBtn.dataComparisonDate1 = vm.returnDateRange(604800, null);
            vm.switchBtn.dataComparisonDate2 = vm.returnDateRange(604800, null);
            break;
        };
      },
      switchDataComponent: function(flg) {
        let vm = this;
        if (vm.switchBtn.dataComponentIndex == flg) { return; }
        if(flg == 'data-stock') {
          vm.componentChart_right.options.series[0].data.forEach((v,i) => {
            v.value = v.value + 80;
          });
        } else {
          vm.componentChart_right.options.series[0].data.forEach((v,i) => {
            v.value = v.value - 80;
          });
        }
        vm.initChart('component-left', vm.componentChart_left.options, vm.componentChart_left.data);
        vm.initChart('component-right', vm.componentChart_right.options, vm.componentChart_right.data);
        vm.switchBtn.dataComponentIndex = flg;
      }
    }
  }
</script>
<style lang="scss">
.input-orange .ivu-input:hover {
    border-color:#ffc86b;
}
.input-orange .ivu-input:focus {
    border-color: #ffc86b;
    outline: 0;
    -webkit-box-shadow: 0 0 0 2px rgba(255,200,107, 0.2);
    box-shadow: 0 0 0 2px rgba(255,200,107, 0.2);
}
.input-orange .ivu-input {
    border-color: #ffc86b;
}
</style>


